.notification {
    font-size: 20px;
    width: 25vw;
    height: 100%;
    margin: auto;
    background-color: lightgray;
    padding: 12px;
    text-align: center;
}
.create-notification{
    border: 2px solid green;
    color: green;
}
.delete-notification{
    border: 2px solid red;
    color: red;
}
.update-notification{
    border: 2px solid blue;
    color: blue;
}

